/*

	Burger menu button

*/

.burger-menu {
  display: block;
  border-top: #343a40 2px solid;
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transition: all 0.3s cubic-bezier(0.34, 1.61, 0.7, 1);
  transition: all 0.3s cubic-bezier(0.34, 1.61, 0.7, 1);
}

.burger-menu:before {
  content: "";
  display: block;
  position: absolute;
  width: 75%;
  top: 50%;
  margin-top: -2px;
  height: 1px;
  border-top: #343a40 2px solid;
  -webkit-transition: all 0.3s cubic-bezier(0.34, 1.61, 0.7, 1);
  transition: all 0.3s cubic-bezier(0.34, 1.61, 0.7, 1);
}

.burger-menu:after {
  content: "";
  display: block;
  position: absolute;
  width: 25%;
  bottom: 0;
  height: 1px;
  border-bottom: #343a40 2px solid;
  -webkit-transition: all 0.3s cubic-bezier(0.34, 1.61, 0.7, 1);
  transition: all 0.3s cubic-bezier(0.34, 1.61, 0.7, 1);
}

