/* 

	Thumbnail : Shadow drop 

*/

.thumbnail-shadow-drop {
  position: relative;
}

.thumbnail-shadow-drop:after {
  position: absolute;
  width: 56%;
  height: 26%;
  left: 20%;
  border-radius: 50%;
  z-index: 1;
  bottom: 128px;
  margin-bottom: 13%;
  content: "";
  -webkit-box-shadow: 0 210px 60px rgba(0, 0, 0, 0.2);
          box-shadow: 0 210px 60px rgba(0, 0, 0, 0.2);
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
  opacity: 0;
  -webkit-animation: __utilFadeInSlow 2s ease;
  animation: __utilFadeInSlow 2s ease;
  -webkit-animation-delay: .5s;
  animation-delay: .5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 4s;
  animation-duration: 4s;
}

.thumbnail-shadow-drop:before {
  position: absolute;
  width: 41%;
  height: 25%;
  left: 23%;
  border-radius: 50%;
  z-index: 0;
  bottom: 25%;
  margin-bottom: 0%;
  content: "";
  -webkit-box-shadow: 0 194px 50px rgba(0, 0, 0, 0.3);
          box-shadow: 0 194px 50px rgba(0, 0, 0, 0.3);
  -webkit-transform: rotate(-17deg);
          transform: rotate(-17deg);
  opacity: 0;
  -webkit-animation: __utilFadeInSlow 2s ease;
  animation: __utilFadeInSlow 2s ease;
  -webkit-animation-delay: .5s;
  animation-delay: .5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 4s;
  animation-duration: 4s;
}

@media only screen and (max-width: 768px) {
  .thumbnail-shadow-drop:before {
    bottom: 45%;
    left: 18%;
  }
}

/*

	Timeline

*/

.timeline-container > div,
.timeline-container {
  position: relative;
}

.timeline-container:before {
  content: '';
  position: absolute;
  width: 4px;
  top: 7px;
  bottom: 7px;
  height: calc(100% - (4px + 3));
  background-color: #e9ecef;
  left: 100px;
}

.timeline-container i.badge {
  width: 17px;
  height: 17px;
  border: transparent 4px solid;
  display: inline-block;
  position: absolute;
  padding: 0;
  top: 7px;
  left: 93.5px;
}

.timeline-container i.badge:after {
  content: '';
  width: 25px;
  height: 25px;
  border: #fff 4px solid;
  position: absolute;
  z-index: 0;
  border-radius: 50rem;
  top: -8px;
  left: -8px;
}

.timeline-container .timeline-content,
.timeline-container .timeline-title {
  position: relative;
  display: block;
  padding-left: 24px;
  margin-left: 100px;
  border-left: transparent 4px solid;
}

/* invert */

.timeline-container.timeline-invert {
  text-align: right;
}

.timeline-container.timeline-invert:before {
  left: auto;
  right: 100px;
}

.timeline-container.timeline-invert i.badge {
  left: auto;
  right: 93.5px;
}

.timeline-container.timeline-invert i.badge:after {
  left: auto;
  right: -8px;
}

.timeline-container.timeline-invert .timeline-content,
.timeline-container.timeline-invert .timeline-title {
  padding-left: 0;
  padding-right: 24px;
  margin-left: 0;
  margin-right: 100px;
  border-left: 0;
  border-right: transparent 4px solid;
}

/**

    Shape : V

**/

.shape-v {
  position: absolute;
  height: 0px;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 1;
}

.shape-v + section,
.shape-v + div {
  z-index: 9;
}

.shape-v:after,
.shape-v:before {
  position: absolute;
  content: '';
  background-color: #fff;
  bottom: 0;
  height: 100px;
  margin-bottom: -100px;
  z-index: 2;
}

.shape-v.bg-light:after,
.shape-v.bg-light:before {
  background-color: #f8f9fa;
}

.shape-v:before {
  left: 0;
  width: 40%;
  -webkit-transform-origin: 100% 0;
          transform-origin: 100% 0;
  -webkit-transform: scaleX(2) scaleY(5) rotate(6deg);
          transform: scaleX(2) scaleY(5) rotate(6deg);
}

.shape-v:after {
  right: 0;
  width: 60%;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleX(2) scaleY(5) rotate(-8deg);
          transform: scaleX(2) scaleY(5) rotate(-8deg);
}

/**
    
    Responsive

**/

@media only screen and (min-width: 992px) {
  /* Shape : V */

  .shape-v.shape-xs:before {
    -webkit-transform: scaleX(2) scaleY(5) rotate(2deg);
            transform: scaleX(2) scaleY(5) rotate(2deg);
  }

  .shape-v.shape-xs:after {
    -webkit-transform: scaleX(2) scaleY(5) rotate(-3deg);
            transform: scaleX(2) scaleY(5) rotate(-3deg);
  }
}

/**

	FORMS

		1. Pill inputs/selects/textarea
		2. Link over input/textarea
		3. Switch
		4. Radio 
		5. Checkbox
