}

.input-group-over > a,
.input-group-over > button,
.input-group-over > span {
  font-size: 15px;
  font-weight: 400;
  text-decoration: none;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 0;
}

/* a div, button, etc */

.el-over-input {
  position: absolute !important;
  top: 0;
  z-index: 5 !important;
  right: 0;
}

/* 

	3. Switch

*/

/* @toggle switch elements 
-------------------------------------------------- */

.form-switch {
  cursor: pointer;
  position: relative;
  display: inline-block;
  min-height: 26px;
  clear: both;
  padding-left: 73px;
  margin-right: 10px;
}

.m-0.form-switch,
.m--0.form-switch {
  padding-left: 58px;
}

.form-switch > i {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-style: normal;
  position: absolute;
  left: 0;
  float: left;
  height: 25px;
  width: 58px;
  color: #fff;
  font-size: 11px;
  text-align: center;
  background: #B5C1C7;
  border: 0 solid #B5C1C7;
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  border-radius: 2px;
}

.form-switch > i:before {
  content: attr(data-off);
  position: absolute;
  top: 3px;
  width: 32px;
  height: 19px;
  line-height: 19px;
  right: 3px;
}

.form-switch > i:after {
  content: "";
  margin: 3px;
  width: 19px;
  height: 19px;
  display: block;
  background: #fff;
  border-radius: 2px;
}

.form-switch > input {
  display: none !important;
}

.form-switch > input:focus + i {
  color: #fff;
  border-color: #a1a6a9;
  background: #a1a6a9;
}

.form-switch > input:focus + i:after {
  background: #fff;
}

/* @toggle switch normal state 
--------------------------------------------------------------- */

.form-switch > input:checked + i {
  border-color: #333;
  background: #333;
  color: #fff;
  padding-left: 33px;
}

.form-switch.form-switch-primary > input:checked + i {
  border-color: #4285F4;
  background: #4285F4;
}

.form-switch.form-switch-secondary > input:checked + i {
  border-color: #6c757d;
  background: #6c757d;
}

.form-switch.form-switch-success > input:checked + i {
  border-color: #6dbb30;
  background: #6dbb30;
}

.form-switch.form-switch-info > input:checked + i {
  border-color: #73e7f7;
  background: #73e7f7;
}

.form-switch.form-switch-warning > input:checked + i {
  border-color: #fad776;
  background: #fad776;
}

.form-switch.form-switch-danger > input:checked + i {
  border-color: #f64e60;
  background: #f64e60;
}

.form-switch.form-switch-light > input:checked + i {
  border-color: #f8f9fa;
  background: #f8f9fa;
}

.form-switch.form-switch-dark > input:checked + i {
  border-color: #2E2E2E;
  background: #2E2E2E;
}

.form-switch.form-switch-indigo > input:checked + i {
  border-color: #533be2;
  background: #533be2;
}

.form-switch.form-switch-purple > input:checked + i {
  border-color: #4c2c92;
  background: #4c2c92;
}

.form-switch.form-switch-pink > input:checked + i {
  border-color: #f92084;
  background: #f92084;
}

.form-switch > input:checked + i:before {
  content: attr(data-on);
  top: 3px;
  height: 19px;
  line-height: 19px;
  color: #fff;
  font-family: 'Zapf Dingbats', 'font-regular', sans-serif;
  /* mobile fix - because some characters like "✔" are emoji rewritten by vedors (apple/android/etc) */
  left: 1px;
}

/* @toggle switch normal state focus 
--------------------------------------------------------------------------------- */

.form-switch-pill > i {
  border-radius: 13px;
}

.form-switch-pill > i:before {
  width: 29px;
}

.form-switch-pill > i:after {
  width: 19px;
  height: 19px;
  color: #B5C1C7;
  content: "\2022";
  font: 20px/20px Times, Serif;
  border-radius: 13px;
  line-height: 19px;
}

.form-switch-pill > input:checked + i {
  padding-left: 33px;
}

.form-switch-pill > input:checked + i:after {
  color: #333;
}

/* disabled */

.form-switch input[disabled] + i {
  border-color: #e9ecef !important;
  background: #e9ecef !important;
}

