.dd-list {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
}

.dd-list .dd-list {
  padding-left: 30px;
}

.dd-collapsed .dd-list {
  display: none;
}

.dd-item,
.dd-empty,
.dd-placeholder {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
}

.dd-item > button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background: transparent;
  text-align: center;
  z-index: 2;
  position: absolute;
  width: 46px;
  height: 46px;
  line-height: 46px;
}

.dd-item > button + .nestable-options + .dd-handle,
.dd-item > button + .dd-handle {
  padding-left: 46px;
}

.dd-item > button:before {
  /*content: '+';*/
  font-family: "Flaticon";
  content: "\F195";
  color: #6c757d;
}

.dd-item > button[data-action="collapse"]:before {
  /*content: '-';*/
  font-family: "Flaticon";
  content: "\F196";
  color: #adb5bd;
}

.dd-placeholder,
.dd-empty {
  margin: 5px 0;
  padding: 10px;
  background: #f2fbff;
  border: 1px dashed #b6bcbf;
  border-radius: 0.25rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.dd-dragel {
  position: absolute;
  pointer-events: none;
  z-index: 9999;
}

.dd-dragel > .dd-item .dd-handle {
  margin-top: 0;
}

/* Custom Handler */

.dd-handle-custom > .dd-content,
.dd-handle {
  display: block;
  position: relative;
  margin: 5px 0;
  padding: 10px;
  font-size: 15px;
  height: 46px;
  background: #fff;
  text-decoration: none;
  font-weight: 500;
  border: 1px solid #dee2e6;
  border-radius: 0.25rem;
}

.dd-handle:hover {
